import React from 'react'
import './sidebar.css'
import { Link } from 'react-router-dom';
import HomeIcon from '@mui/icons-material/Home';
import PersonIcon from '@mui/icons-material/Person';
import SchoolIcon from '@mui/icons-material/School';
import AssignmentIcon from '@mui/icons-material/Assignment';
import AssessmentIcon from '@mui/icons-material/Assessment';
import ArticleIcon from '@mui/icons-material/Article';
import MailIcon from '@mui/icons-material/Mail';
import img from './images/sk1.jpg'
const Sidebar = () => {
  
  return (
    <>
    
    <div className="desc">
            <div className="profile">
                <img src={img} alt="" />
            </div>
            <div className="option">
              <Link to='/' className='span'><div className="myseet"><HomeIcon className='icon'/> Home</div></Link>
              <Link to='/about' className='span'><div className="myseet"><PersonIcon  className='icon'/> About</div></Link>
              <Link to='/education' className='span'><div className="myseet"><SchoolIcon  className='icon'/>Education</div></Link>
              <Link to='/skill' className='span'><div className="myseet"><AssessmentIcon  className='icon'/>Skills</div></Link>
              <Link to='/project' className='span'><div className="myseet"><AssignmentIcon  className='icon'/>Projects</div></Link>
              <Link to='/resume' className='span'><div className="myseet"><ArticleIcon  className='icon'/> Resume</div></Link>
              <Link to='/contact' className='span'><div className="myseet"><MailIcon  className='icon'/> Contact</div></Link>
             
            </div>
        </div>
        </>
  );
}

export default Sidebar