import React from 'react'
import './contact.css'
import MailIcon from '@mui/icons-material/Mail';
import GitHubIcon from '@mui/icons-material/GitHub';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import PhoneIcon from '@mui/icons-material/Phone';
import {Link} from 'react-router-dom'
const Contact = () => {
  return (
    <div className="contact">
    <div className="head">CONTACTS</div>
        <div className="contactcont">
            <p><PhoneIcon className='contacticon'/> <span className='mobno'>+91 8084789776</span></p>
            <p><Link><div><MailIcon className='contacticon' /> <span>skm808478@gmail.com</span></div></Link></p>
            <p><Link to='https://github.com/shashankkr20'><div><GitHubIcon className='contacticon'/> <span>https://github.com/shashankkr20</span></div></Link></p>
            <p><Link to='https://www.linkedin.com/in/shashank-kumar-4b6a80206'><div><LinkedInIcon className='contacticon'/><span>http://surl.li/guvga</span></div></Link></p>
        </div>
    </div>
  )
}

export default Contact