import React from 'react'
import './skill.css'
import html from './images/skill/html5.png'
import css from './images/skill/css3.png'
import js from './images/skill/javascript.png'
import mysql from './images/skill/mysql.jpg'
import python from './images/skill/python.png'
import cpp from './images/skill/cpp.png'
import java from './images/skill/java.png'
import mongo from './images/skill/mongo1.jpg'
import boot from './images/skill/bootstrap.png'
import react from './images/skill/react.png'
import node from './images/skill/node.webp'
import express from './images/skill/exp.png'
import git from './images/skill/git.png'
import github from './images/skill/github.png'
import excel from './images/skill/excel.png'
const Skill = () => {
  return (
    <div className="skills">
    <div className="head">SKILLS</div>
    <div className="skillset">
        <span>Language and Databases</span>
        <div className="skillcontents">
        <div className="skillnames"><img src={cpp} alt="" /><span>C++</span></div>
        <div className="skillnames"><img src={python} alt="" /><span>Python</span></div>
        <div className="skillnames"><img src={java} alt="" /><span>Java</span></div>
            <div className="skillnames"><img src={html} alt="" /><span>Html</span></div>
            <div className="skillnames"><img src={css} alt="" /><span>Css</span></div>
            <div className="skillnames"><img src={js} alt="" /><span>Javascript</span></div>
            <div className="skillnames"><img src={mysql} alt="" /><span>Mysql</span></div>
            <div className="skillnames"><img src={mongo} alt="" /><span>MongoDB</span></div>
        </div>
        
    </div>
    <div className="skillset">
    <span>Framework</span>
        <div className="skillcontents">
        <div className="skillnames"><img src={boot} alt="" /><span>Bootstrap</span></div>
            <div className="skillnames"><img src={react} alt="" /><span>React</span></div>
            <div className="skillnames"><img src={node} alt="" /><span>Nodejs</span></div>
            <div className="skillnames"><img src={express} alt="" /><span>Expressjs</span></div>
        </div>
    </div>
    <div className="skillset">
    <span>Others</span>
        <div className="skillcontents">
        <div className="skillnames"><img src={git} alt="" /><span>Git</span></div>
            <div className="skillnames"><img src={github} alt="" /><span>Github</span></div>
            <div className="skillnames"><img src={excel} alt="" /><span>MS Excel</span></div>
        </div>
    </div>
    </div>
  )
}

export default Skill