import React from 'react'
import './scrollside.css'
import { Link } from 'react-router-dom';
import HomeIcon from '@mui/icons-material/Home';
import PersonIcon from '@mui/icons-material/Person';
import SchoolIcon from '@mui/icons-material/School';
import AssignmentIcon from '@mui/icons-material/Assignment';
import AssessmentIcon from '@mui/icons-material/Assessment';
import ArticleIcon from '@mui/icons-material/Article';
import MailIcon from '@mui/icons-material/Mail';
import cut from './images/close2.png'
import img from './images/sk1.jpg'
const Scrollside = () => {
    const optionHandler=(e)=>{
        e.preventDefault();
            document.querySelector(".scrside").classList.add("animate");
            document.querySelector(".scrside").classList.remove("animatecl");
    }
    const closeHand=(e)=>{
        e.preventDefault();
        document.querySelector(".scrside").classList.add("animatecl");
        document.querySelector(".scrside").classList.remove("animate");
    }
  return (
    <>
    <div className='upprbar'><span>Shashank Kumar</span><div className='opt' onClick={optionHandler}><div className="opt1"></div><div className="opt1" ></div><div className="opt1"></div></div></div>
    <div className='scrside'>
        <img src={cut} alt="" className='closescr' onClick={closeHand}/>
            <div className="profile">
                <img src={img} alt="" />
            </div>
            <div className="option">
              <Link to='/' className='span'><HomeIcon className='icon'/> Home</Link>
              <Link to='/about' className='span'><PersonIcon  className='icon'/> About</Link>
              <Link to='/education' className='span'><SchoolIcon  className='icon'/>Education</Link>
              <Link to='/skill' className='span'><AssessmentIcon  className='icon'/>Skills</Link>
              <Link to='/project' className='span'><AssignmentIcon  className='icon'/>Projects</Link>
              <Link to='/resume' className='span'><ArticleIcon  className='icon'/> Resume</Link>
              <Link to='/contact' className='span'><MailIcon  className='icon'/> Contact</Link>
             
            </div>
    </div>
    </>
  )
}

export default Scrollside